import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-profile-family-members',
  templateUrl: './profile-family-members.component.html',
  styleUrls: ['./profile-family-members.component.scss']
})
export class ProfileFamilyMembersComponent implements OnInit {
  
  isDataLoaded:boolean = false;

  familyDataList:any;

  userId:any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
  	private userService:UserService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  getParams(event){

  	if(event){

      this.isDataLoaded = false;
      let getFamilyDetails = {
        apartmentBlockUnitUserId : this.userId
      }
  		this.userService.getUserFamilyMembersByUserId(getFamilyDetails).subscribe((res:any) => {

	  		this.familyDataList = res.filter(item => { return item.isActive });;
	  		this.isDataLoaded = true;

  		})

  	}
  }

  isFamilyMembersAvailable(){
  	return this.familyDataList.length != 0 ? true : false;
  }

  ngOnInit() {

  	if(this.route.params['value'].id != undefined){
      this.userId = this.route.params['value'].id
    }
    else {
      this.userId = parseInt(this.cookieService.get('userId'))
    }


  	this.userService.getUserFamilyMembersByUserId(this.userId).subscribe((res:any) => {

  		this.familyDataList = res.filter(item => { return item.isActive });;
  		this.isDataLoaded = true;

  	})

  }

}
