import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-profile-family-members-details',
  templateUrl: './profile-family-members-details.component.html',
  styleUrls: ['./profile-family-members-details.component.scss']
})
export class ProfileFamilyMembersDetailsComponent implements OnInit {
  
  isDataLoaded:boolean = false;
  isEdit:boolean = false;

  @Input() isFamilyNew?:boolean;
  @Input() user?:any;

  bloodGroupData:any;
  genderType:string = "";

  userId:any;

  @Output() public outputParams = new EventEmitter();

  constructor(
    private router: Router,
    private route: ActivatedRoute,
  	private userService:UserService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  showEditForm(){
    this.isEdit = !this.isEdit;
  }

  getGender(id){
  	return id == '43' ? 'Male' : 'Female'
  }

  getDOB(date){
  	if(date === undefined || date.length == 0) {
  		return ""
  	}
  	else {
  		return moment(date).format("DD-MM-YY");
  	}
    
  }

  deleteMember(){

    let params = {
      userFamilyMemberId: parseInt(this.user.userFamilyMemberId),
      deleteBy: parseInt(this.cookieService.get('userId'))
    }

    this.userService.deleteUserFamilyMember(params).subscribe((res:any) => {

      if(res.message){
        this.isDataLoaded = true;  
        this.sharedService.setAlertMessage("Member deleted");
        this.outputParams.emit(true);
      }
      else {
        this.isDataLoaded = true;
      }

    })

  }


  submitProfileFamilyForm(form){
  	
    if(!this.isEdit){

      let details = {

        "firstName": this.user.firstName,
        "middleName": this.user.middleName,
        "lastName": this.user.lastName,
        "relationship": this.user.relationship,
        "relatedUserId": this.userId,
        "emailId": this.user.emailId,
        "genderId": parseInt(this.genderType),
        "dob": this.user.dob,
        "bloodGroup": this.user.bloodGroup,
        "phoneNumber": this.user.phoneNumber.toString(),
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": 0,
        "updatedOn": "2020-03-28T10:44:31.345Z"
        // "apartmentBlockUnitUserId":

      }

      let params = {
        userFamilyMember: details
      }

      this.userService.addUserFamilyMember(params).subscribe((res:any) => {

        if(res.message){
          this.isDataLoaded = true;  
          this.sharedService.setAlertMessage("Family Member added successfully");
          this.outputParams.emit(true);
        }
        else {
          this.isDataLoaded = true;
        }

      })

    }
    else {

      let details = {

        "userFamilyMemberId": this.user.userFamilyMemberId,
        "firstName": this.user.firstName,
        "middleName": this.user.middleName,
        "lastName": this.user.lastName,
        "relationship": this.user.relationship,
        "relatedUserId": this.userId,
        "emailId": this.user.emailId,
        "genderId": parseInt(this.genderType),
        "dob": this.user.dob,
        "bloodGroup": this.user.bloodGroup,
        "phoneNumber": this.user.phoneNumber.toString(),
        "isActive": this.user.isActive,
        "insertedBy": this.user.insertedBy,
        "insertedOn": this.user.insertedOn,
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()

      }

      let params = {
        userFamilyMember: details
      }

      this.userService.updateUserFamilyMember(params).subscribe((res:any) => {

        if(res.message){
          this.isDataLoaded = true;  
          this.sharedService.setAlertMessage("Family Member updated successfully");
          this.outputParams.emit(true);
        }
        else {
          this.isDataLoaded = true;
        }

      })

    }

  }


  ngOnInit(): void {

    if(this.route.params['value'].id != undefined){
      this.userId = this.route.params['value'].id
    }
    else {
      this.userId = parseInt(this.cookieService.get('userId'))
    }

    let groupParams = {
      LookupTypeId: 24
    }

    this.lookupService.getLookupValueByLookupTypeId(groupParams).subscribe((res:any) => {
      this.bloodGroupData = res;
    });

    if(this.isFamilyNew){
      this.user = {};
      this.user.bloodGroup = "";
    }
    else {
      this.genderType = '' + this.user.genderId;
    }

    this.isDataLoaded = true;

  }

}
