import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-profile-unit-details',
  templateUrl: './profile-unit-details.component.html',
  styleUrls: ['./profile-unit-details.component.scss']
})
export class ProfileUnitDetailsComponent implements OnInit {
  
  isUserDataLoaded:boolean = false;
  isEdit:boolean = false;

  block:any;

  blockData:any;
  blockUnitData:any;
  unitTypeData:any;

  blockUnitId:any;
  unitTypeId:any;

  unitListData:any;

  unitNameList:any;

  userUnitId:string = "";

  apartmentBlockUnitUserId:any;

  isBlockSelected:boolean = false;

  isUnitSelected:boolean = false;

  userType:any;

  isError:boolean = false;
  errorMessage:string = "";

  @Input() userId?:any;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
  	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  showEditForm(){
  	this.isEdit = !this.isEdit;
    this.isBlockSelected = false;
  }

  getUnits(id){

    let params = {
      apartmentBlockId: parseInt(id)
    }

  	this.apartmentService.getApartmentBlockUnitByBlockId(params).subscribe((res:any) => {
  	    	
      	this.blockUnitData = res;
        this.isBlockSelected = true;

    });

  }

  getUnitType(name){

  	var data = _.filter( this.unitTypeData, function( item ) {
      if(item.lookupValueName === name)
        return item;
    });

    if(data === undefined || data.length == 0){
      this.unitTypeId = null;
    }
    else {
      this.unitTypeId = data[0].lookupValueId;
    }


  }

  getUnitName(id){

    var data = _.filter( this.unitNameList, function( item ) {
      
      if(item.apartmentBlockUnitId === id){
        return item;
      }
    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].apartmentBlockUnitNumber;
    }

  }


  showUnitDetails(id){

    var data = _.filter( this.unitNameList, function( item ) {
            
    if(item.apartmentBlockUnitId == id){
        return item;
      }
    });
    
    this.isUnitSelected = true;

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      this.block = data[0];
      this.getUnits(this.block.apartmentBlockId);
      this.getUnitType(this.block.unitType);
      this.getUnitUserId(id);
    }

    console.log(this.block);

  }

  getUnitUserId(id) {
    
    var data = _.filter( this.unitListData, function( item ) {
      
      if(item.apartmentBlockUnitId == id){
          return item;
        }

      });

      if(data === undefined || data.length == 0){
          return '';
      }
      else {
        this.apartmentBlockUnitUserId = data[0].apartmentBlockUnitUserId;
      }

      

  }


  submitProfileUnitForm(form){

  	this.isUserDataLoaded = false;

    let apartmentBlockUnitUser = {

      "apartmentBlockUnitUserId": parseInt(this.apartmentBlockUnitUserId),
      "apartmentBlockUnitId": parseInt(this.block.apartmentBlockUnitId),
      "userId": parseInt(this.cookieService.get('userId')),
      "userTypeId": parseInt(this.userType),
      "isPrimaryContact": true,
      "isActive": this.block.isActive,
      "insertedBy": 0,
      "insertedOn": "2019-12-14T07:01:44.740Z",
      "updatedBy": parseInt(this.cookieService.get('userId')),
      "updatedOn": "2019-12-14T07:01:44.740Z"

    }

    let apartmentBlockUnitUserParams = {
      apartmentBlockUnitUser: apartmentBlockUnitUser
    }

    this.apartmentService.updateApartmentBlockUnitUser(apartmentBlockUnitUserParams).subscribe((res:any) => {

      if(res.message) {


        let details = {
          "apartmentBlockUnitId": parseInt(this.block.apartmentBlockUnitId),
          "apartmentBlockUnitNumber": this.block.apartmentBlockUnitNumber,
          "apartmentBlockId": parseInt(this.block.apartmentBlockId),
          "unitTypeId": this.unitTypeId,
          "builtupArea": this.block.builtupArea,
          "carpetArea": this.block.carpetArea,
          "intercom": this.block.intercom,
          "cars": parseInt(this.block.cars),
          "isAvailableForRent": this.block.isAvailableForRent,
          "availableDate": "2019-12-13T07:07:59.550Z",
          "rentAmount": this.block.rentAmount,
          "advanceAmount": this.block.advanceAmount,
          "isPetsAllowed": this.block.isPetsAllowed,
          "isFurnished": this.block.isFurnished,
          "isVacant": this.block.isVacant,
          "propertyManagerId": null,
          "isActive": this.block.isActive,
          "insertedBy": this.block.insertedBy,
          "insertedOn": this.block.insertedOn,
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": new Date().toISOString()

      }

      let apartmentBlockUnitParams = {
        apartmentBlockUnit: details
      }

        this.apartmentService.updateApartmentBlockUnit(apartmentBlockUnitParams).subscribe((res:any) => {
          
          if(res.message) {
            this.isUserDataLoaded = true;
            this.isEdit = false;
            this.sharedService.setAlertMessage("Unit updated successfully");
            this.isBlockSelected = false;
          }
          else {

            this.isError = true;
            this.errorMessage = res.errorMessage;

          }

        },
        error => {
          
        });


      }

      else {

        this.isError = true;
        this.errorMessage = res.errorMessage;

      }

    },
    error => {
      this.isError = true;
      this.errorMessage = "Some error occured";
    }
    );


  	

  }

  ngOnInit() {

  	this.block = {};

    if(this.route.params['value'].id != undefined){
      this.userId = this.route.params['value'].id
    }
    else {
      this.userId = parseInt(this.cookieService.get('userId'))
    }

    this.unitNameList = [];

  	let blockParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.apartmentService.getApartmentBlockByApartmentId(blockParams).subscribe((res:any) => {
      this.blockData = res;
    });
   let queryParamBase = {
      userId: this.userId,
    };
    // get role
    this.userService.getRolesByUserId(queryParamBase).subscribe((data:any) => {

      this.userType = data[0].roleId.toString();

    },
    error => {
      
    });

    let unitTypeParams = {
      LookupTypeId: 1
    }

    this.lookupService.getLookupValueByLookupTypeId(unitTypeParams).subscribe((res:any) => {
    	
    	this.unitTypeData = res;

      let queryParamBase = {
        userId: this.userId,
      };
      this.apartmentService.getAllApartmentBlockUnitUsersByUserId(queryParamBase).subscribe((unit:any) => {

        this.unitListData = unit;

        

        _.each(this.unitListData, item => {
        let  queryParamBase = {
            apartmentBlockUnitId: item.apartmentBlockUnitId
          };
          this.apartmentService.getApartmentBlockUnitById(queryParamBase).subscribe((data:any) => {

            this.unitNameList.push(data[0]);
            this.isUserDataLoaded = true;
            let id = this.unitListData[0].apartmentBlockUnitId;
            this.showUnitDetails(id);

          },
          error => {

          });

        })

      },
      error => {
        
      }); 

    	
    });
    
  }



}
