import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { PetService } from 'src/app/api/controllers/Pet';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-profile-pet',
  templateUrl: './profile-pet.component.html',
  styleUrls: ['./profile-pet.component.scss']
})
export class ProfilePetComponent implements OnInit {

  isUserDataLoaded:boolean = false;
  isUnitSelected:boolean = false;

  blockData:any;
  blockUnitData:any;
  unitTypeData:any;

  userUnitId:string = "";

  unitListData:any;
  unitNameList:any;

  isError:boolean = false;
  errorMessage:string = "";

  petDataList:any;
  pet:any;

  isPetsAvailable:boolean = false;

  @Input() userId?:any;
  
  petTypeList:any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private petService:PetService,
  	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  showUnitDetails(id){


    this.isUserDataLoaded = false;
    this.isUnitSelected = true;

    let petListParams = {
      apartmentBlockUnitId: parseInt(id)
    }

    this.petService.getAllPetsByApartmentBlockUnitId(petListParams).subscribe((res:any) => {

      if(res.length != 0){
        this.petDataList = res.filter(item => { return item.pet.isActive });
        this.isPetsAvailable = true;
      }
      else {
        this.isPetsAvailable = false;
      }

      this.isUserDataLoaded = true;      

    })


  }

  getUnitName(id){

    var data = _.filter( this.unitNameList, function( item ) {
      
      if(item.apartmentBlockUnitId === id){
        return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].apartmentBlockUnitNumber;
    }

  }

  getPetType(id){

  	var data = _.filter( this.petTypeList, function( item ) {
      
      if(item.lookupValueId === id){
      	return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  getDate(date){
  	if(date === undefined || date.length == 0) {
  		return ""
  	}
  	else {
  		return moment(date).format("DD-MM-YY");
  	}
    
  }

  getParams(event){
    this.isUserDataLoaded = false;      
  	if(event){
      let petListParams = {
        apartmentBlockUnitId: parseInt(this.userUnitId)
      }

  	  this.petService.getAllPetsByApartmentBlockUnitId(petListParams).subscribe((res:any) => {

	      if(res.length != 0){
	        this.petDataList = res.filter(item => { return item.pet.isActive });
	        this.isPetsAvailable = true;
	      }
	      else {
	        this.isPetsAvailable = false;
	      }

	      this.isUserDataLoaded = true;      

      })
      this.isUserDataLoaded = true;      
  	}

  }

  isPetsNotAvailable(){
    return this.petDataList.length == 0 ? true : false; 
  }
  

  ngOnInit() {


    if(this.route.params['value'].id != undefined){
      this.userId = this.route.params['value'].id
    }
    else {
      this.userId = parseInt(this.cookieService.get('userId'))
    }

    this.unitNameList = [];

    let blockParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.apartmentService.getApartmentBlockByApartmentId(blockParams).subscribe((res:any) => {
      this.blockData = res;
    });

    let petParams = {
      LookupTypeId: 85
    }

    this.lookupService.getLookupValueByLookupTypeId(petParams).subscribe((res:any) => {
      this.petTypeList = res;
    });

    let unitParams = {
      LookupTypeId: 1
    }

    this.lookupService.getLookupValueByLookupTypeId(unitParams).subscribe((res:any) => {
    	
      this.unitTypeData = res;
      let getAllApartmentParam ={
        userId:this.userId
      }
      this.apartmentService.getAllApartmentBlockUnitUsersByUserId(getAllApartmentParam).subscribe((unit:any) => {
        this.unitListData = unit;
        _.each(this.unitListData, item => {
          let getBlockUnitId = {
            apartmentBlockUnitId:item.apartmentBlockUnitId
          }
          this.apartmentService.getApartmentBlockUnitById(getBlockUnitId).subscribe((data:any) => {
            this.unitNameList.push(data[0]);
            this.isUserDataLoaded = true;
          },
          error => {

          });

        })

      },
      error => {
        
      }); 

    	
    });

    this.isUserDataLoaded = true;
  

  }

}
