import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-profile-vehicles',
  templateUrl: './profile-vehicles.component.html',
  styleUrls: ['./profile-vehicles.component.scss']
})
export class ProfileVehiclesComponent implements OnInit {
  
  isUserDataLoaded:boolean = false;
  isUnitSelected:boolean = false;

  blockData:any;
  blockUnitData:any;
  unitTypeData:any;

  userUnitId:string = "";

  unitListData:any;
  unitNameList:any;

  isError:boolean = false;
  errorMessage:string = "";

  vehicleDataList:any;
  vehicle:any;

  vehicleTypeList:any;

  isVehicleAvailable:boolean = false;

  @Input() userId?:any;
  
  parkingSlotsDataList:any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private vehicleService:VehicleService,
  	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  showUnitDetails(id){


    this.isUserDataLoaded = false;
    this.isUnitSelected = true;

    let vehicleListParams = {
      apartmentBlockUnitId: parseInt(id)
    }

    this.vehicleService.getAllVehiclesByApartmentBlockUnitId(vehicleListParams).subscribe((res:any) => {

      this.vehicleDataList = res;
      this.isUserDataLoaded = true;      

    })

    let parkingListParams = {
      apartmentIdBlockUnitId: parseInt(id)
    }

    this.vehicleService.getParkingSlotByApartmentIdUnitId(parkingListParams).subscribe((res:any) => {

      this.parkingSlotsDataList = res;

    });


  }

  getVehicleType(id){

    var data = _.filter( this.vehicleTypeList, function( item ) {
      
      if(item.lookupValueId === id){
        return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  getUnitName(id){

    var data = _.filter( this.unitNameList, function( item ) {
      
      if(item.apartmentBlockUnitId === id){
        return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].apartmentBlockUnitNumber;
    }

  }

  getParams(event){

    if(event){

      let params = {
        apartmentBlockUnitId: parseInt(this.userUnitId)
      }

      this.vehicleService.getAllVehiclesByApartmentBlockUnitId(params).subscribe((res:any) => {

        this.vehicleDataList = res;
        this.isUserDataLoaded = true;      

      })

    }

  }

  isPlotNotAvailable(){
    return this.parkingSlotsDataList == 0 ? true : false;
  }

  isVehicleNotAvailable(){
    return this.vehicleDataList == 0 ? true : false;
  }
  

  ngOnInit() {


    if(this.route.params['value'].id != undefined){
      this.userId = this.route.params['value'].id
    }
    else {
      this.userId = parseInt(this.cookieService.get('userId'))
    }

    this.unitNameList = [];

  	let blockParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.apartmentService.getApartmentBlockByApartmentId(blockParams).subscribe((res:any) => {
      this.blockData = res;
    });

    let vehicleTypeParams = {
      LookupTypeId: 2
    }

    this.lookupService.getLookupValueByLookupTypeId(vehicleTypeParams).subscribe((res:any) => {

      this.vehicleTypeList = res;

    });

      let unitTypeParams = {
      LookupTypeId: 1
    }

    this.lookupService.getLookupValueByLookupTypeId(unitTypeParams).subscribe((res:any) => {
    	
    	this.unitTypeData = res;

      
      this.apartmentService.getAllApartmentBlockUnitUsersByUserId(this.userId).subscribe((unit:any) => {

        this.unitListData = unit;



        _.each(this.unitListData, item => {

          this.apartmentService.getApartmentBlockUnitById(item.apartmentBlockUnitId).subscribe((data:any) => {


            this.unitNameList.push(data[0]);
            this.isUserDataLoaded = true;
            let id = this.unitListData[0].apartmentBlockUnitId;
            this.showUnitDetails(id);          

          },
          error => {

          });

        })

      },
      error => {
        
      }); 

    	
    });


  

  }

}
