import { Component, OnInit, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

import { UserService } from 'src/app/api/controllers/User';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';
import { MatTableDataSource } from '@angular/material/table';

import * as _ from 'underscore';
import * as moment from 'moment';
import { SharedToasterService } from 'src/app/shared/services/shared-toaster.service';
import { UserDashboardModule } from '../../../../user/user-dashboard/user-dashboard.module';
import { ApartmentService } from '../../../../api/controllers/Apartment';


export interface PeriodicElement {
  name: string;
  position: number;
  weight: number;
  symbol: string;
}


@Component({
  selector: 'app-unapproved',
  templateUrl: './unapproved.component.html',
  styleUrls: ['./unapproved.component.scss']
})
export class UnapprovedComponent implements OnInit {

  ELEMENT_DATA: any;
  dataSource: any;
  displayedColumns: string[] = ['position', 'name', 'weight', 'symbol'];


  unitListData: any;
  logsData: any;

  ItemUserStartIndex: any = 0;
  ItemUserEndIndex: any;
  totalUserItems: any;
  itemUserLimit: any = 15;

  ItemLogStartIndex: any = 0;
  ItemLogEndIndex: any;
  totalLogItems: any;
  itemLogLimit: any = 15;

  unitData: string = "";

  unitFieldType: string = "unitno";
  unitOrder: boolean = false;

  isMobile: boolean = false;

  modalService: any;

  selectAllUnapprovedUser: boolean = false;

  isUserSelected: boolean = false;

  isLogs: boolean = false;

  isUserDataLoaded: boolean = false;

  isLogsDataLoaded: boolean = false;

  isProfile: boolean = false;

  viewUserId: any;

  selectedInput: string = "";
  columnField: any = {}

  columnData: any;
  gridSourceData: any;
  isDefaultSelected: boolean = false;
  selectAllDefaulters: boolean = false;
  checkedList: Array<any> = [];

  @ViewChild("viewUnapprovedUserRef", { static: false }) viewUnapprovedUserRef: TemplateRef<any>;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
    private router: Router,
    private userService: UserService,
    private sharedService: SharedService,
    private cookieService: CookieService,
    private srvModal: ModalService,
    private apartmentService : ApartmentService,
    private SharedToaster: SharedToasterService,
  ) {
    this.modalService = this.injector.get(ModalService);
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }



  getUserIndexParams(event) {
    // this.ItemUserStartIndex = event.ItemUserStartIndex;
    // this.ItemUserEndIndex = event.ItemUserEndIndex;
    // this.itemUserLimit = event.ItemLimit;

    this.ItemUserStartIndex = event.ItemStartIndex;
    this.ItemUserEndIndex = event.ItemEndIndex;
    this.itemUserLimit = event.itemLimit;

    // this.ItemStartIndex = event.ItemStartIndex;
    // this.ItemEndIndex = event.ItemEndIndex;
    // this.itemLimit = event.itemLimit;

    // if(this.totalUserItems>this.itemUserLimit){
    //   this.ItemUserEndIndex = this.itemUserLimit;
    // }
    // else {
    //   this.ItemUserEndIndex = this.totalUserItems;
    // }

  }
  onDateChange(event, type) {

    if (event != null) {
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }

  }
  getBookedDate(itr, date) {

    _.each(this.unitListData, (obj, index) => {
      obj.facilityBookedForDate = moment(obj.bookedForDate).format("DD/MM/YYYY");
    });

    return moment(date).format("DD/MM/YYYY");
  }
  getLogIndexParams(event) {
    this.ItemLogStartIndex = event.ItemLogStartIndex;
    this.ItemLogEndIndex = event.ItemLogEndIndex;
  }

  selectColInput(value) {
    console.log(value);
    this.selectedInput = value;
  }

  getBlockNo(item, data) {
    this.unitListData.map(obj => {
      if (obj.id == item.id) {
        obj.apartmentBlockNumber = data;
      }
    });
    return data;
  }

  getBlockUnitNo(item, data) {
    this.unitListData.map(obj => {
      if (obj.id == item.id) {
        obj.apartmentBlockUnitNumber = data;
      }
    });
    return data;
  }

  getNotes(data) {
    if (data === undefined || data.length == 0)
      return '';
    else
      return data[0].notes;
  }

  getUserRole(role, index) {
    if (role === undefined || role.length == 0) {
      return '';
    }
    else {
      this.unitListData[index].roleName = role[0].roleName;
      return role[0].roleName;
    }

  }

  getUserRoleInfo(role) {
    if (role.userRole === undefined || role.userRole.length == 0) {
      return '';
    }
    else {
      return role.userRole[0].roleName;
    }
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {

    if (event.target.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;

  }


  @HostListener('window:onCheckDefaulterHeader', ['$event.detail'])
  onCheckDefaulterHeader(detail) {

    let allDataRecords = this.datagrid.getrows()

    _.each(allDataRecords, item => {
      item.checked = detail.checked
    })
    this.checkedList = allDataRecords;

    // this.isDefaultSelected = allDataRecords.some(item => {
    //   return item.checked
    // })
    this.datagrid.refresh();
    this.getSelectedUnapprovedUser();
  }

  @HostListener('window:onCheckDefaulterRow', ['$event.detail'])
  onCheckDefaulterRow(detail) {
    let allDataRecords = this.datagrid.getrows()
    var dataRecord = this.datagrid.getrowdata(detail.rowId)
    dataRecord.checked = detail.checked;
    this.checkedList = allDataRecords;
    // this.isDefaultSelected = allDataRecords.some(item => {
    //   return item.checked
    // })
    this.getSelectedUnapprovedUser();

  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  sortUnitData(type) {
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if (this.unitFieldType == type) {
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  getAllUnapprovedUser() {

    if (this.selectAllUnapprovedUser) {
      _.each(this.unitListData, (item) => {
        item.checked = true;
      })
      this.isUserSelected = true;
    }
    else {
      _.each(this.unitListData, (item) => {
        item.checked = false;
      })
      this.isUserSelected = false;
    }

  }

  getSelectedUnapprovedUser() {
    var length = 0;
    _.each(this.checkedList, (item) => {
      if (item.checked) {
        length++;
      }
    })
    if (length > 0) {
      this.isUserSelected = true;
    }
    else {
      this.isUserSelected = false;
    }

    if (length == 0) {
      this.selectAllUnapprovedUser = false;
    }

    if (length == this.unitListData.length) {
      this.selectAllUnapprovedUser = true;
    }

  }

  approveUsers() {
    this.isUserDataLoaded = false;
    _.each(this.checkedList, (item, index) => {
      if (item.checked) {
        let getUserParam = {
          userid: item.id
        }
        this.userService.getUserById(getUserParam).subscribe((res: any) => {
          var user = res[0];
          let updateUserParam = {
            user: user
          }
          this.userService.updateUser(updateUserParam).subscribe((res: any) => {
            let updateApartmentBlock = {
              "apartmentBlockUnitUserId": 0,
              "apartmentBlockUnitId": 0,
              "userId": user.userId,
              "userTypeId": 0,
              "isPrimaryContact": true,
              "isBillToParty": true,
              "isActive": true,
              "insertedBy": 0,
              "insertedOn": "2020-06-22T06:02:49.605Z",
              "updatedBy": 0,
              "updatedOn": "2020-06-22T06:02:49.605Z",
              "isLiving": true,
              "isDocSubmitted": true,
              "readyforApproval": true,
              "deleteReasonId": 0,
              "comments": "",
              "isDeletePermanently": false,
              "isapproved": true,
              "moveInDate": null,
              "moveOutDate": null
            }

            let updateApartmentBlockParam = {
              apartmentBlockUnitUser : updateApartmentBlock
            }
            this.apartmentService.updateApartmentBlockUnitUser(updateApartmentBlockParam).subscribe(
              resp =>{
                this.SharedToaster.openSnackBar("Approved", '');
                this.isUserDataLoaded = true;
              }
            )
          }
          )
        });
      }
      if (this.checkedList.length == index + 1) {
        this.getUnApprovedList();
      }
    });
    this.isUserSelected = !this.isUserSelected;
  }

  showConfirmModal(id) {
    this.modalService.showConfirmModal(id);
  }

  showUserInfo(user) {

    var userId = user.id;

    var blockData = user.apartmentBlockUnit;

    var userRole = user.userRole;

    this.userService.getUserById(userId).subscribe((res: any) => {

      var userData = res[0];

      if (blockData === undefined || blockData.length == 0) {
        userData.blockNo = "Not Available";
        userData.unitNo = "";
      }
      else {
        userData.blockNo = blockData[0].apartmentBlockNumber;
        userData.unitNo = blockData[0].apartmentBlockUnitNumber;
      }

      if (userRole === undefined || userRole.length == 0) {
        userData.roleName = "Not Available";
      }
      else {
        userData.roleName = userRole[0].roleName;
      }

      this.dialog.open(this.viewUnapprovedUserRef, {
        panelClass: 'material',
        disableClose: true,
        data: userData
      });


    });



  }

  getParams(event) {
    this.isProfile = event;
  }

  selectedTab() {
    setTimeout(() => {
      var elem = document.querySelector('.user-info-card');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);
  }

  viewUserInfo(id) {
    console.log(id);
    this.isProfile = false;

    setTimeout(() => {

      this.isProfile = true;

    }, 10);


    setTimeout(() => {

      var elem = document.querySelector('.user-info-card');
      var scrollTo = elem.getBoundingClientRect().top - 100;

      window.scroll({
        top: scrollTo,
        behavior: 'smooth'
      });

    }, 100);


    this.viewUserId = id;

    this.router.navigate(['ams/unit&users/unapproved/basic', this.viewUserId]);



  }

  showLogs() {

    this.isLogs = !this.isLogs;

    this.sharedService.getJsonData().subscribe((res: any) => {

      this.logsData = res.logsData;

      this.totalLogItems = this.logsData.length;

      if (this.totalLogItems > this.itemLogLimit) {
        this.ItemLogEndIndex = this.itemLogLimit;
      }
      else {
        this.ItemLogEndIndex = this.totalLogItems;
      }

      this.isLogsDataLoaded = true;

    },
      error => {
        console.log(error);
      }
    );


  }

  onGlSearchFilter() {

    if (this.unitData != "") {

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.unitData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if (item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();

    }
    else {
      this.datagrid.clearfilters();
    }

  }

  @HostListener('window:onUnapproveDelete', ['$event.detail'])
  onUnapproveDelete(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let unapprovedId = dataRecord.id
    this.modalService.showConfirmModal(unapprovedId);
  }

  @HostListener('window:onviewUserInfo', ['$event.detail'])
  onviewUserInfo(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    this.viewUserInfo(dataRecord.id)
    // let unapprovedId = dataRecord.unit.idd
    // this.modalService.showConfirmModal(unapprovedId);
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
      text: '',
      datafield: 'checked',
      width: 80,
      pinned: true,
      sortable: false,
      menu: false,
      cellsrenderer: (row, column, value) => {
        var chkedProperty;
        if (value) {
          chkedProperty = 'checked="true"';
        }
        else {
          chkedProperty = '';
        }
        return '<div class="jqx-custom-inner-cell">'
          + '<div class="form-group mb-0 w-100">'
          + '<div class="form-check text-center">'
          + '<input type="checkbox" class="form-check-input" onClick="checkDefaulterRowEvent(' + row + ', this.checked)" id="defaultChecker' + row + '" name="defaultChecker' + row + '" ' + chkedProperty + '>'
          + '<label class="form-check-label" for="defaultChecker' + row + '"></label>'
          + '</div>'
          + '</div>'
          + '</div>'
      },
      renderer: (value) => {
        return '<div style="padding: 10px">'
          + '<div class="form-group mb-0 w-100">'
          + '<div class="form-check check-header text-center">'
          + '<input type="checkbox" id="selectAllDefaulters" name="selectAllDefaulters" onClick="checkDefaulterHeaderEvent(event, this.checked)" >'
          + '<label class="form-check-label" for="selectAllDefaulters"></label>'
          + '</div>'
          + '</div>'
          + '</div>'
      }
    }, {
      text: 'Name',
      datafield: 'name',
      minwidth: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Phone',
      datafield: 'phone',
      cellsrenderer: cellsrenderer,
      width: 200,
      renderer: columnrenderer
    }, {
      text: 'Email',
      datafield: 'email',
      cellsrenderer: cellsrenderer,
      width: 200,
      renderer: columnrenderer
    }, {
      text: 'Block  No',
      datafield: 'apartmentBlockNumberJQ',
      cellsrenderer: cellsrenderer,
      width: 200,
      renderer: columnrenderer
    }, {
      text: 'Unit No',
      datafield: 'apartmentBlockUnitNumberJQ',
      cellsrenderer: cellsrenderer,
      width: 200,
      renderer: columnrenderer
    }, {
      text: 'Requested On',
      datafield: 'insertedOn',
      cellsrenderer: cellsrenderer,
      width: 200,
      renderer: columnrenderer
    }, {
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="viewUserInfo(' + row + ')"><i class="fa fa-eye icon eye" aria-hidden="true"></i></a><a href="javascript:void(0)" onClick="showConfirmDeleteEvent(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
    }];
    this.getUnApprovedList();
    if (window.innerWidth <= 991)
      this.isMobile = true;
    else
      this.isMobile = false;



    // delete user
    this.sharedService.unitlistdeleteindexcast.subscribe(id => {

      if (id != null) {

        var params = {
          userId: id,
          updateUserId: parseInt(this.cookieService.get('userId'))
        }


        this.userService.deleteUserById(params).subscribe((res: any) => {

          _.each(this.unitListData, (type) => {
            if (type.id == id) {
              type.active = false;
            }
          })

          setTimeout(() => {

            this.unitListData = this.unitListData.filter((type) => type.id !== id);
            this.totalUserItems = this.unitListData.length;
            this.sharedService.setAlertMessage("User deleted");
            this.sharedService.setUnitListDeleteIndex(null);

          }, 500)

        }, error => {
          console.log(error);
        });


      }

    });

  }

  getUnApprovedList() {
    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.userService.getAllUnApprovedUsersByApartmentId(params).subscribe((res: any) => {

      var unitListData = res;
      console.log(res);
      //filter active true items
      this.unitListData = unitListData.filter(data => {
        data.insertedOn = new Date(data.insertedOn).toLocaleDateString()
        data.apartmentBlockNumberJQ = data.apartmentBlockUnit[0].apartmentBlockNumber
        data.apartmentBlockUnitNumberJQ = data.apartmentBlockUnit[0].apartmentBlockUnitNumber
        return data.active
      })

      setTimeout(() => {

        console.log(this.unitListData);
      }, 2000);

      this.gridSourceData = {
        localdata: unitListData,
        datatype: "array"
      }

      this.unitListData = new jqx.dataAdapter(this.gridSourceData);
      this.totalUserItems = this.unitListData.length;

      if (this.totalUserItems > this.itemUserLimit) {
        this.ItemUserEndIndex = this.itemUserLimit;
      }
      else {
        this.ItemUserEndIndex = this.totalUserItems;
      }

      this.isUserDataLoaded = true;

    },
      error => {
        console.log(error);
      }
    );
  }
  editUserInfo(data) {
    this.srvModal.showUnApprovedetailsModal(data);
  }
  public getColor(docstatus): string {
    return docstatus ? "red" : "yellow";
  }
}

function showConfirmDeleteEvent(row) {
  var event = new CustomEvent('onUnapproveDelete', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDeleteEvent = showConfirmDeleteEvent;

function viewUserInfo(row) {
  var event = new CustomEvent('onviewUserInfo', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewUserInfo = viewUserInfo;


function checkDefaulterHeaderEvent(event, isChecked) {

  event.stopPropagation();
  var newEvent = new CustomEvent('onCheckDefaulterHeader', {
    detail: {
      checked: isChecked
    }
  })
  window.dispatchEvent(newEvent);

}
(window as any).checkDefaulterHeaderEvent = checkDefaulterHeaderEvent;

function checkDefaulterRowEvent(row, isChecked) {

  var event = new CustomEvent('onCheckDefaulterRow', {
    detail: {
      rowId: row,
      checked: isChecked
    }
  })
  window.dispatchEvent(event);
}
(window as any).checkDefaulterRowEvent = checkDefaulterRowEvent;