import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';
import { UserService } from 'src/app/api/controllers/User';
import { RentService } from 'src/app/api/controllers/Rent';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CreateLeaseInfoComponent } from './create-lease-info/create-lease-info.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-profile-lease-info',
  templateUrl: './profile-lease-info.component.html',
  styleUrls: ['./profile-lease-info.component.scss']
})
export class ProfileLeaseInfoComponent implements OnInit {
  
  isDataLoaded:boolean = false;
  rent: any;
  rentListData: any;
  apartmentBlockUnitId: any;
  userId: any;
  modalService:any;
  isEdit: boolean = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private rentService:RentService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private dialog: MatDialog,
  ) { }

  ngOnInit() {
    this.rent = {};
    this.userId =  this.route.params['value'].id;
    let params = {
      userId: parseInt(this.userId)
    }

    this.apartmentService.getApartmentBlockUnitByUserId(params).subscribe((res : any) => {
      let data = res[0] || {};
      this.apartmentBlockUnitId = data.apartmentBlockUnitId;
      this.getListData();
       
     },
  error => {
    console.log(error);
  });
    
  }

   getListData(){
    this.rentService.getAllApartmentBlockUnitRents().subscribe((res : any) => {
     
      this.rentListData = res.filter(data => {
        data.startDate = moment(data.startDate).format('MM/DD/YYYY');
        data.expiryDate = moment(data.expiryDate).format('MM/DD/YYYY');
        return data.isActive && data.apartmentBlockUnitId == this.apartmentBlockUnitId
      })
        let length = this.rentListData.length - 1;
        this.rent = this.rentListData[length];
        this.isDataLoaded = true;
     },
  error => {
          console.log(error);
  });
  }

submitLeaseInfoForm(form){
  this.isDataLoaded = false;
  let details = {
    "apartmentBlockUnitId": this.apartmentBlockUnitId,
    "startDate": this.rent.startDate,
    "expiryDate": this.rent.expiryDate,
    "rentAmount": parseInt(this.rent.rentAmount),
    "advanceAmount": parseInt(this.rent.advanceAmount),
    "leaseAmount": parseInt(this.rent.leaseAmount),
    "rentTypeId": 376,
    "notes": "string",
    "field1": "string",
    "field2": "string",
    "isActive": true,
    "insertedBy": this.userId,
    "insertedOn": moment(new Date()).utc().format().toString(),
    "updatedBy": this.userId,
    "updatedOn": moment(new Date()).utc().format().toString()
  }
  let AddApartmentBlockUnitRentParams = {
      apartmentBlockUnitRent: details
  }
  this.rentService.addApartmentBlockUnitRent(AddApartmentBlockUnitRentParams).subscribe((res : any) => {
      if(res.message){
      this.rent = {};
      this.sharedService.setAlertMessage("Lease Info added successfully");
      this.getListData();
      this.isEdit = false;
      }
      this.isDataLoaded = true;
   },
error => {
  this.isDataLoaded = true;
        console.log(error);
});
}

showEditForm(type) {
  this.isEdit = !this.isEdit;
  if(type == 'reset') {
    this.rent = {}
  }else{
    let length = this.rentListData.length - 1;
    this.rent = this.rentListData[length];
  }
}
 
showHistory() {
  const dialogRef = this.dialog.open(CreateLeaseInfoComponent, {
    width: '400px',
    data: {
      title: 'Rental History',
      label_1: 'Start Date',
      label_2: 'Expiry Date',
      data: this.rentListData
    }
  });
}
}

