import { Component, OnInit, Input, OnChanges, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { PetService } from 'src/app/api/controllers/Pet';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-profile-pet-details',
  templateUrl: './profile-pet-details.component.html',
  styleUrls: ['./profile-pet-details.component.scss']
})
export class ProfilePetDetailsComponent implements OnInit {

  @Input() petData?:any;
  @Input() isPetNew?:boolean;

  @Input() apartmentBlockUnitId:any;

  isDataLoaded:boolean = false;

  isEdit:boolean = false;

  petTypeList:any;

  vehicleUnit:any;

  petUnit:any;

  pet:any;

  userId:any;

  @Output() public outputParams = new EventEmitter();
  
  constructor(
    private router: Router,
    private route: ActivatedRoute,
  	private userService:UserService,
    private petService:PetService,
  	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  showEditForm(){
  	this.isEdit = !this.isEdit;
  }
  

  getPetType(id){

  	var data = _.filter( this.petTypeList, function( item ) {
      
      if(item.lookupValueId === id){
      	return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  getDate(date){
  	if(date === undefined || date.length == 0) {
  		return ""
  	}
  	else {
  		return moment(date).format("DD-MM-YY");
  	}
    
  }

  deletePet(){

    let params = {
      petId: parseInt(this.pet.petId),
      deleteBy: parseInt(this.cookieService.get('userId'))
    }

    this.petService.deletePet(params).subscribe((res:any) => {

      if(res.message){
        this.isDataLoaded = true;  
        this.sharedService.setAlertMessage("Pet deleted");
        this.outputParams.emit(true);
      }
      else {
        this.isDataLoaded = true;
      }

    })

  }

  submitProfilPetForm(form){

    this.isDataLoaded = false;

    if(!this.isEdit){

      let details = {
  
		"petTypeId": parseInt(this.pet.petTypeId),
		"dob": this.pet.dob,
		"isActive": true,
		"insertedBy": parseInt(this.cookieService.get('userId')),
		"insertedOn": new Date().toISOString(),
		"updatedBy": 0,
		"updatedOn": "2020-03-27T07:50:54.680Z"

      }

      let params = {
        pet: details
      }

      this.petService.addPet(params).subscribe((res:any) => {


            if(res.message){

              var unitDetails = {
                
        				"apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
        				"petId": res.message,
        				"isActive": true,
        				"insertedBy": parseInt(this.cookieService.get('userId')),
        				"insertedOn": new Date().toISOString(),
        				"updatedBy": 0,
        				"updatedOn": "2020-03-27T07:52:53.767Z"

              }

              let params = {
                apartmentBlockUnitPet: unitDetails
              }

              this.petService.addApartmentBlockUnitPet(params).subscribe((res:any) => {

                if(res.message){
                  this.isDataLoaded = true;  
                  this.sharedService.setAlertMessage("Pet added successfully");
                  this.outputParams.emit(true);
                }
                else {
                  this.isDataLoaded = true;
                }

              })

           }
           else {
              this.isDataLoaded = true;
           }

      })

    }
    else {

      let details = {
  		
        "petId": this.pet.petId,
        "petTypeId": parseInt(this.pet.petTypeId),
        "dob": this.pet.dob,
        "isActive": this.pet.isActive,
        "insertedBy": this.pet.insertedBy,
        "insertedOn": this.pet.insertedOn,
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()

      }

      let params = {
        pet: details
      }

      this.petService.updatePet(params).subscribe((res:any) => {


        if(res.message){

              var unitDetails = {
                 
                "apartmentBlockUnitPetId": parseInt(this.petUnit.apartmentBlockUnitPetId),
                "apartmentBlockUnitId": parseInt(this.petUnit.apartmentBlockUnitId),
                "petId": parseInt(this.pet.petId),
                "isActive": this.petUnit.isActive,
                "insertedBy": this.petUnit.insertedBy,
                "insertedOn": this.petUnit.insertedOn,
                "updatedBy": parseInt(this.cookieService.get('userId')),
                "updatedOn": new Date().toISOString()

              }

              let params = {
                apartmentBlockUnitPet: unitDetails
              }

              this.petService.updateApartmentBlockUnitPet(params).subscribe((res:any) => {

                if(res.message){
                  this.isDataLoaded = true;  
                  this.sharedService.setAlertMessage("Pet updated successfully");
                  this.outputParams.emit(true);
                }
                else {
                  this.isDataLoaded = true;
                }

              })

           }
         else {
            this.isDataLoaded = true;
         }

      })

    }
  	

  }

  ngOnInit() {

    if(this.route.params['value'].id != undefined){
      this.userId = this.route.params['value'].id
    }
    else {
      this.userId = parseInt(this.cookieService.get('userId'))
    }

    this.pet = {};

    if(this.petData != undefined){
      this.petUnit = this.petData
      this.pet = this.petData.pet;
    }
    else {
      this.pet.petTypeId = "";
    }

    this.isDataLoaded = true;

    let petTypeParams = {
      LookupTypeId: 85
    }

    this.lookupService.getLookupValueByLookupTypeId(petTypeParams).subscribe((res:any) => {
      this.petTypeList = res;
    });


  }

  ngOnChanges(changes: SimpleChanges) {
     
  }

}
