import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { MyProfileComponent } from './my-profile.component';
import { ProfileVehiclesComponent } from './components/profile-vehicles/profile-vehicles.component';
import { ProfileFamilyMembersComponent } from './components/profile-family-members/profile-family-members.component';
import { ProfilePetComponent } from './components/profile-pet/profile-pet.component';
import { PropertyManagerComponent } from './components/property-manager/property-manager.component';
import { ProfileUnitDetailsComponent } from './components/profile-unit-details/profile-unit-details.component';
import { ProfileUnitStaffsComponent } from './components/profile-unit-staffs/profile-unit-staffs.component';
import { ProfileLeaseInfoComponent } from './components/profile-lease-info/profile-lease-info.component';
import { ProfileInterestGroupComponent } from './components/profile-interest-group/profile-interest-group.component';
import { ProfileBasicComponent } from './components/profile-basic/profile-basic.component';
import { ProfileUserAbsenceComponent } from './components/profile-user-absence/profile-user-absence.component';
import { PropertyManagerCreateComponent } from './components/property-manager/property-manager-create/property-manager-create.component';

const routes: Routes = [
	
	{ path: '', component: MyProfileComponent,
	
		children: [
			{ path: '', redirectTo: 'basic', pathMatch: 'full'},
			{ path: 'basic', component: ProfileBasicComponent},
			{ path: 'basic/:id', component: ProfileBasicComponent},
			{ path: 'unit/:id', component: ProfileUnitDetailsComponent},
			{ path: 'staffs/:id', component: ProfileUnitStaffsComponent},
			{ path: 'vehicles/:id', component: ProfileVehiclesComponent },
			{ path: 'pets/:id', component: ProfilePetComponent },
			{ path: 'propertymanager/:id', component: PropertyManagerComponent},
			{ path: 'propertymanager/create', component: PropertyManagerCreateComponent},
			{ path: 'family/:id', component: ProfileFamilyMembersComponent},
			{ path: 'lease/:id', component: ProfileLeaseInfoComponent},
			{ path: 'interest/:id', component: ProfileInterestGroupComponent},
			{ path: 'absence/:id', component: ProfileUserAbsenceComponent},
			{ path: '**', redirectTo: 'basic', pathMatch: 'full'}
		]

	},
	{ path: 'myprofile/basic', component: ProfileBasicComponent},
	{ path: 'myprofile/basic/:id', component: ProfileBasicComponent},
	{ path: 'myprofile/unit/:id', component: ProfileUnitDetailsComponent},
	{ path: 'myprofile/staffs/:id', component: ProfileUnitStaffsComponent},
	{ path: 'myprofile/vehicles/:id', component: ProfileVehiclesComponent },
	{ path: 'myprofile/pets/:id', component: ProfilePetComponent },
	{ path: 'myprofile/propertymanager/:id', component: PropertyManagerComponent},
	{ path: 'myprofile/propertymanager/create', component: PropertyManagerCreateComponent},
	{ path: 'myprofile/family/:id', component: ProfileFamilyMembersComponent},
	{ path: 'myprofile/lease/:id', component: ProfileLeaseInfoComponent},
	{ path: 'myprofile/interest/:id', component: ProfileInterestGroupComponent},
	{ path: 'myprofile/absence/:id', component: ProfileUserAbsenceComponent}
	
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MyProfileRoutingModule { }
