import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';


import { UserService } from 'src/app/api/controllers/User';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.scss']
})
export class MyProfileComponent implements OnInit {

  user: any;
  userRole: any;

  isUserDataLoaded: boolean = false;

  apartmentBlockUnitId: any;

  @Input() type?: any;
  @Input() isProfile?: boolean;
  @Input() userId?: any;

  @Output() public outputParams = new EventEmitter();
  @Output() public tabSelection = new EventEmitter();

  constructor(
    private userService: UserService,
    private sharedService: SharedService
  ) { }


  isAdmin() {
    return this.type == 'admin' ? true : false
  }

  closeUserInfo() {
    this.isProfile = false;
    this.outputParams.emit(this.isProfile);
  }

  ngOnInit() {

    this.user = {};

    this.sharedService.userdetailscast.subscribe(user => {
      this.user = user;
      if(user != null)
      this.isUserDataLoaded = true;

    });


  }

  ngOnChanges() {

    if(this.isAdmin()) {
      
      let params = {
        userid: this.userId
      }

      this.isUserDataLoaded = false;
  
      this.userService.getUserById(params).subscribe((res: any) => {
  
        this.user = res[0];

        if(this.user.image != null){
          var imagePath = 'data:image/png;base64,'+this.user.image;
          this.sharedService.setUserPic(imagePath)
        }
        else {
          var imagePath = 'assets/images/user-icon.svg'
          this.sharedService.setUserPic(imagePath)
        }

  
        let params = {
          userId: this.userId
        }
  
        this.userService.getRolesByUserId(params).subscribe((role: any) => {
  
          this.user.roleName = role[0].roleName
          this.isUserDataLoaded = true;
  
        },
        error => {
          console.log(error);
        });
  
      },
      error => {
        console.log(error);
      });
    }

  }

}
