import { Component, OnInit, Injector } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CreateLeaseInfoComponent } from '../profile-lease-info/create-lease-info/create-lease-info.component';
import { MatDialog } from '@angular/material/dialog';
import * as _ from 'underscore';

@Component({
  selector: 'app-profile-user-absence',
  templateUrl: './profile-user-absence.component.html',
  styleUrls: ['./profile-user-absence.component.scss']
})
export class ProfileUserAbsenceComponent implements OnInit {
data: any;
userId: any;
unitListData: any;
apartmentBlockUnitUserId: any;
apartmentBlockUnitId: any;
isDataLoaded: boolean = false;
isEdit: boolean = false;
historyListData: any;

constructor(
  private router: Router,
  private route: ActivatedRoute,
  private injector: Injector,
  private apartmentService:ApartmentService,
  private lookupService: LookupService,
  private sharedService:SharedService,
  private cookieService:CookieService,
  private dialog: MatDialog,
) { 
  
}

  ngOnInit() {
    this.data = {};
    this.userId = this.route.params['value'].id;
    let queryParamBase = {
      userId: this.userId,
    };
    
    this.apartmentService.getAllApartmentBlockUnitUsersByUserId(queryParamBase).subscribe((unit:any) => {
      this.unitListData = unit;
      this.apartmentService.getApartmentBlockUnitByUserId(queryParamBase).subscribe((res : any) => {
        let data = res[0] || {};
        this.apartmentBlockUnitId = data.apartmentBlockUnitId;
        this.getUnitUserId(this.apartmentBlockUnitId);
         
       },
    error => {
      console.log(error);
    });
    },
    error => {

    });
  }
  getUnitUserId(id) {
    
    var data = _.filter( this.unitListData, function( item ) {
      
      if(item.apartmentBlockUnitId == id){
          return item;
        }

      });

      if(data === undefined || data.length == 0){
          return '';
      }
      else {
        this.apartmentBlockUnitUserId = data[0].apartmentBlockUnitUserId;
        this.getHistoryData();
      }
    
  }

  getHistoryData(){
    let params = {
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "ApartmentBlockUnitUserId": this.apartmentBlockUnitUserId
  }
  this.apartmentService.getOutofCondobyApartmentblockunituser(params).subscribe((unit:any) => {
    this.historyListData = unit.filter(data => {
      data.outFrom = moment(data.outFrom).format('MM/DD/YYYY');
    data.outTo = moment(data.outTo).format('MM/DD/YYYY');
    data.startDate = moment(data.outFrom).format('MM/DD/YYYY');
    data.expiryDate = moment(data.outTo).format('MM/DD/YYYY');
    return data.isActive;
  })
  let length = this.historyListData.length - 1;
        this.data = this.historyListData[length];
        this.isDataLoaded = true;
    },
    error => {
      
    });
  }

  showEditForm(type) {
    this.isEdit = !this.isEdit;
    if(type == 'reset') {
      this.data = {}
    }else{
      let length = this.historyListData.length - 1;
      this.data = this.historyListData[length];
    }
  }

  showHistory() {
    const dialogRef = this.dialog.open(CreateLeaseInfoComponent, {
      width: '400px',
      data: {
        title: 'Out Of Condo History',
        label_1: 'From Date',
        label_2: 'To Date',
        data: this.historyListData
      }
    });
  }

  submitAbsenceForm(form) {
    this.isDataLoaded = false;

    let details = {
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "apartmentBlockUnitId": this.apartmentBlockUnitId,
      "outFrom": this.data.outFrom,
      "outTo": this.data.outTo,
      "localContactP1": this.data.localContactP1,
      "localContactP1phone": this.data.localContactP1phone,
      "localcontactP1alternatephone": this.data.localcontactP1alternatephone,
      "localcontactP1email": this.data.localcontactP1email,
      "localContactP2": this.data.localContactP2,
      "localContactP2phone": this.data.localContactP2phone,
      "localcontactP2alternatephone": this.data.localcontactP2alternatephone,
      "localcontactP2email": this.data.localcontactP2email,
      "outofCondoStatusId": 5,
      "isActive": true,
      "insertedBy": this.userId,
      "insertedOn": moment(new Date()).utc().format().toString(),
      "updatedBy": this.userId,
      "updatedOn": moment(new Date()).utc().format().toString(),
      "apartmentBlockUnitUserId": this.apartmentBlockUnitUserId
    }

    let params = {
      apartmentLongAbsence: details
    }
    this.apartmentService.addApartmentLongAbsence(params).subscribe((data:any) => {
          if(data.message) {
            this.data = {};
            this.sharedService.setAlertMessage("Out of condo Info added successfully");
            this.getHistoryData();
            this.isEdit = false;
          }   
          this.isDataLoaded = true;
    },
    error => {
      this.isDataLoaded = false;
    });
  }
}
