import { Component, OnInit, Input, OnChanges, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { VehicleService } from 'src/app/api/controllers/Vehicle';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-profile-vehicle-details',
  templateUrl: './profile-vehicle-details.component.html',
  styleUrls: ['./profile-vehicle-details.component.scss']
})
export class ProfileVehicleDetailsComponent implements OnInit {
  
  @Input() vehicleData?:any;
  @Input() isVehicleNew?:boolean;

  @Input() apartmentBlockUnitId:any;

  isDataLoaded:boolean = false;

  isEdit:boolean = false;

  vehicleTypeList:any;

  vehicleUnit:any;

  vehicle:any;

  userId:any;

  @Output() public outputParams = new EventEmitter();
  
  constructor(
    private router: Router,
    private route: ActivatedRoute,
  	private userService:UserService,
    private vehicleService:VehicleService,
  	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  showEditForm(){
  	this.isEdit = !this.isEdit;
  }
  

  getVehicleType(id){

  	var data = _.filter( this.vehicleTypeList, function( item ) {
      
      if(item.lookupValueId === id){
      	return item;
      }

    });


    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  submitProfilVehicleForm(form){

    this.isDataLoaded = false;

    if(!this.isEdit){

      let details = {
  
        "vehicleNumber": this.vehicle.vehicleNumber,
        "vehicleTypeId": parseInt(this.vehicle.vehicleTypeId),
        "vehicleManufacturer": this.vehicle.vehicleManufacturer,
        "vehicleModel": this.vehicle.vehicleModel,
        "vehicleDriverNumber": this.vehicle.vehicleDriverNumber,
        "vehicleColor": this.vehicle.vehicleColor,
        "isActive":  this.vehicle.isActive,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": 0,
        "updatedOn": "2020-03-26T20:23:25.938Z"

      }

      let vehicleParams = {
        vehicle: details
      }

      this.vehicleService.addVehicle(vehicleParams).subscribe((res:any) => {


            if(res.message){

              var unitDetails = {
                
                "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
                "vehicleId": res.message,
                "userId": parseInt(this.userId),
                "documentId": null,
                "parkingSlotId": null,
                "stickerNumber": "string",
                "rfid": "string",
                "fromDate": "2020-03-26T20:30:26.576Z",
                "toDate": "2020-03-26T20:30:26.576Z",
                "isPassIssued": true,
                "isActive": true,
                "insertedBy": parseInt(this.cookieService.get('userId')),
                "insertedOn": new Date().toISOString(),
                "updatedBy": 0,
                "updatedOn": "2020-03-26T20:30:26.576Z"

              }

              let unitParams = {
                apartmentBlockUnitVehicle: unitDetails
              }

              this.vehicleService.addApartmentBlockUnitVehicle(unitParams).subscribe((res:any) => {

                if(res.message){
                  this.isDataLoaded = true;  
                  this.sharedService.setAlertMessage("Vehicle added successfully");
                  this.outputParams.emit(true);
                }
                else {
                  this.isDataLoaded = true;
                }

              })

           }
           else {
              this.isDataLoaded = true;
           }

      })

    }
    else {

      let details = {
        
        "vehicleId": parseInt(this.vehicle.vehicleId),
        "vehicleNumber": this.vehicle.vehicleNumber,
        "vehicleTypeId": parseInt(this.vehicle.vehicleTypeId),
        "vehicleManufacturer": this.vehicle.vehicleManufacturer,
        "vehicleModel": this.vehicle.vehicleModel,
        "vehicleDriverNumber": this.vehicle.vehicleDriverNumber,
        "vehicleColor": this.vehicle.vehicleColor,
        "isActive":  this.vehicle.isActive,
        "insertedBy": this.vehicle.insertedBy,
        "insertedOn": this.vehicle.insertedOn,
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()

      }

      let vehicleParams = {
        vehicle: details
      }

      this.vehicleService.updateVehicle(vehicleParams).subscribe((res:any) => {


           if(res.message){

              var unitDetails = {
                
                "apartmentBlockUnitVehicleId": parseInt(this.vehicleUnit.apartmentBlockUnitVehicleId),
                "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
                "vehicleId": parseInt(this.vehicle.vehicleId),
                "userId": parseInt(this.userId),
                "documentId": null,
                "parkingSlotId": null,
                "stickerNumber": "string",
                "rfid": "string",
                "fromDate": "2020-03-26T20:30:26.576Z",
                "toDate": "2020-03-26T20:30:26.576Z",
                "isPassIssued": this.vehicle.isPassIssued,
                "isActive": this.vehicle.isActive,
                "insertedBy": this.vehicle.insertedBy,
                "insertedOn": this.vehicle.insertedOn,
                "updatedBy": parseInt(this.cookieService.get('userId')),
                "updatedOn": new Date().toISOString()

              }

              let unitParams = {
                apartmentBlockUnitVehicle: unitDetails
              }

              this.vehicleService.updateApartmentBlockUnitVehicle(unitParams).subscribe((res:any) => {

                if(res.message){
                  this.isDataLoaded = true;  
                  this.sharedService.setAlertMessage("Vehicle updated successfully");
                  this.outputParams.emit(true);
                }
                else {
                  this.isDataLoaded = true;
                }

              })

           }
           else {
              this.isDataLoaded = true;
           }

      })

    }
  	

  }

  ngOnInit() {

    if(this.route.params['value'].id != undefined){
      this.userId = this.route.params['value'].id
    }
    else {
      this.userId = parseInt(this.cookieService.get('userId'))
    }

    this.vehicleUnit = {};

    this.vehicle = {};

    if(this.vehicleData != undefined){
      this.vehicleUnit = this.vehicleData;
      this.vehicle = this.vehicleData.vehicle;
    }
    else {
      this.vehicle.vehicleTypeId = "";
    }

    this.isDataLoaded = true;

    let vehcileTypeParams = {
      LookupTypeId: 2
    }

    this.lookupService.getLookupValueByLookupTypeId(vehcileTypeParams).subscribe((res:any) => {

      this.vehicleTypeList = res;

    });


  }

  ngOnChanges(changes: SimpleChanges) {
    
  }

}
